import { Action } from '@ngrx/store';
import { Post } from '../shared/models/post.model';

export enum PostActionTypes {
  LoadPosts = '[Post] Load Posts',
  ClearPosts = '[Post] Clear Posts',
  SelectPost = '[Post] Select Post',
  LoadOnePost = '[Post] Load One Post',
}

export class LoadPosts implements Action {
  readonly type = PostActionTypes.LoadPosts;

  constructor(public payload: { posts: Post[] }) {}
}

export class ClearPosts implements Action {
  readonly type = PostActionTypes.ClearPosts;
}

export class SelectPost implements Action {
  readonly type = PostActionTypes.SelectPost;

  constructor(public payload: { id: string }) {}
}

export class LoadOnePost implements Action {
  readonly type = PostActionTypes.LoadOnePost;

  constructor(public payload: { post: Post }) {}
}

export type PostActions =
  | LoadPosts
  | ClearPosts
  | SelectPost
  | LoadOnePost;
