import { PageInfo } from '../shared/models/page-info.model';
import { PostPageActionTypes, PostPageActions } from '../actions/post-page.actions';

export interface State extends PageInfo {
  category: string | null;
  loading: boolean;
}

export const initialState: State = {
  startCursor: null,
  endCursor: null,
  hasNextPage: false,
  hasPreviousPage: false,
  category: null,
  loading: false
};

export function reducer(state = initialState, action: PostPageActions): State {
  switch (action.type) {
    case PostPageActionTypes.LoadPage: {
      return {
        ...state,
        category: action.payload.category || null,
        loading: true
      };
    }

    case PostPageActionTypes.LoadPageSuccess: {
      return {
        ...state,
        ...action.payload.pageInfo,
        loading: false
      };
    }

    case PostPageActionTypes.GoNextPage: {
      return {
        ...state,
        loading: true
      };
    }

    case PostPageActionTypes.GoPrevPage: {
      return {
        ...state,
        loading: true
      };
    }

    default:
      return state;
  }
}
