function extractFrom(body: string, meta: string) {
  let reg = new RegExp(`<\\!--\\s*${meta}\\s*-->\\r\\n([\\s\\S]*)\\r\\n<\\!--\\s*${meta}\\s*-->`);
  return body.match(reg);
}

export function extractSummary(body: string) {
  let match = extractFrom(body, 'summary');
  if (match && match[1]) {
    return match[1].replace(/>\s/, '');
  }

  return null;
}

export function extractBanner(body: string) {
  let match = extractFrom(body, 'banner');
  if (match && match[1]) {
    let banner = match[1];
    let url = banner.match(/.*\((.*)\)/);

    if (url && url[1]) {
      return url[1];
    }
  }

  return null;
}
