import { Injectable } from '@angular/core';
import { Store, select } from '@ngrx/store';
import { Actions, Effect, ofType } from '@ngrx/effects';
import { of } from 'rxjs';
import { map, exhaustMap, tap, withLatestFrom, filter } from 'rxjs/operators';
import { PostsService } from '../shared/services/posts.service';
import { State } from '../reducers';

import {
  SelectPost,
  LoadOnePost,
  PostActions,
  PostActionTypes
} from '../actions/post.actions';

import { Post } from '../shared/models/post.model';
import * as fromRoot from '../reducers';

@Injectable()
export class PostEffects {

  @Effect()
  selectPost$ = this.actions$.pipe(
    ofType(PostActionTypes.SelectPost),
    withLatestFrom<SelectPost, Post>(this.store.pipe(select(fromRoot.selectCurrentPost))),
    filter(([action, post]) => (!post && !!action.payload.id)),
    exhaustMap(([action]) =>
      this.posts
        .getPost(action.payload.id)
        .pipe(map(post => new LoadOnePost({ post })))
    )
  );

  constructor(
    private actions$: Actions,
    private posts: PostsService,
    private store: Store<State>
  ) {}
}
