import { Action } from '@ngrx/store';
import { PageInfo, PostPage } from '../shared/models/post-page.model';

export enum PostPageActionTypes {
  LoadPage = '[Post Page] Load Page',
  LoadPageSuccess = '[Post Page] Load Page Success',
  GoNextPage = '[Post Page] Go Next Page',
  GoPrevPage = '[Post Page] Go Prev Page',
}

export class LoadPage implements Action {
  readonly type = PostPageActionTypes.LoadPage;
  constructor(public payload: { category?: string }) {}
}

export class LoadPageSuccess implements Action {
  readonly type = PostPageActionTypes.LoadPageSuccess;
  constructor(public payload: PostPage) {}
}

export class GoNextPage implements Action {
  readonly type = PostPageActionTypes.GoNextPage;
}

export class GoPrevPage implements Action {
  readonly type = PostPageActionTypes.GoPrevPage;
}

export type PostPageActions =
  | LoadPage
  | LoadPageSuccess
  | GoNextPage
  | GoPrevPage;
