import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { HttpClientModule } from '@angular/common/http';
import { MarkdownModule } from 'ngx-markdown';
import { TooltipModule } from 'ngx-bootstrap/tooltip';

import { AppRoutingModule } from './app-routing.module';
import { AppComponent } from './app.component';

import { StoreModule } from '@ngrx/store';
import { reducers, metaReducers } from './reducers';
import { StoreDevtoolsModule } from '@ngrx/store-devtools';
import {
  StoreRouterConnectingModule,
  RouterStateSerializer,
} from '@ngrx/router-store';
import { EffectsModule } from '@ngrx/effects';
import { AppEffects } from './effects/app.effects';
import { PostPageEffects } from './effects/post-page.effects';
import { PostEffects } from './effects/post.effects';

import { environment } from '../environments/environment';
import { LayoutComponent } from './layout/layout.component';
import { FooterComponent } from './components/footer/footer.component';
import { HeaderComponent } from './components/header/header.component';
import { UserBlockComponent } from './components/user-block/user-block.component';
import { LabelBlockComponent } from './components/label-block/label-block.component';
import { HomeComponent } from './views/home/home.component';
import { PostPreviewComponent } from './components/post-preview/post-preview.component';
import { PostEmptyComponent } from './components/post-empty/post-empty.component';
import { PostsComponent } from './components/posts/posts.component';
import { PostDetailComponent } from './components/post-detail/post-detail.component';
import { ReadPostComponent } from './views/read-post/read-post.component';
import { CustomRouterStateSerializer } from './shared/utils/router-state';
import { FilteredPageComponent } from './views/filtered-page/filtered-page.component';

@NgModule({
  declarations: [
    AppComponent,
    LayoutComponent,
    FooterComponent,
    HeaderComponent,
    UserBlockComponent,
    LabelBlockComponent,
    HomeComponent,
    PostPreviewComponent,
    PostEmptyComponent,
    PostsComponent,
    PostDetailComponent,
    ReadPostComponent,
    FilteredPageComponent
  ],
  imports: [
    BrowserModule,
    AppRoutingModule,
    StoreModule.forRoot(reducers, { metaReducers }),
    StoreRouterConnectingModule.forRoot({ stateKey: 'router' }),
    !environment.production ? StoreDevtoolsModule.instrument() : [],
    EffectsModule.forRoot([AppEffects, PostPageEffects, PostEffects]),
    HttpClientModule,
    MarkdownModule.forRoot(),
    TooltipModule.forRoot(),
  ],
  providers: [
    { provide: RouterStateSerializer, useClass: CustomRouterStateSerializer },
  ],
  bootstrap: [AppComponent]
})
export class AppModule { }
