import { Component, OnInit } from '@angular/core';
import { Store, select } from '@ngrx/store';
import { filter, map, take, tap } from 'rxjs/operators';

import * as fromRoot from '../../reducers';
import { SelectPost } from '../../actions/post.actions';

@Component({
  selector: 'blog-read-post',
  templateUrl: './read-post.component.html',
  styleUrls: ['./read-post.component.scss']
})
export class ReadPostComponent implements OnInit {
  post$ = this.store.pipe(
    select(fromRoot.selectCurrentPost),
    filter(post => !!post)
  );

  constructor(private store: Store<fromRoot.State>) { }

  ngOnInit() {
    window.scrollTo(0, 0);

    this.store.pipe(
      select(fromRoot.selectRouterParam),
      map(param => param.id),
      filter(id => !!id),
      tap(id => this.store.dispatch(new SelectPost({ id }))),
      take(1)
    ).subscribe();
  }

}
