import { Injectable } from '@angular/core';
import { Observable, of } from 'rxjs';
import { map, catchError } from 'rxjs/operators';
import * as moment from 'moment';
import { GraphqlService } from './graphql.service';
import { PostPage, Post, PageInfo } from '../models/post-page.model';
import { extractSummary, extractBanner } from '../utils/extract-post';

@Injectable({
  providedIn: 'root'
})
export class PostsService {
  readonly itemPerPage = 5;

  constructor(private graphql: GraphqlService) { }

  private getEmptyPage(): PostPage {
    return {
      posts: [],
      pageInfo: {
        startCursor: null,
        endCursor: null,
        hasNextPage: false,
        hasPreviousPage: false,
      }
    };
  }

  private getPostFrom(node): Post {
    if (node) {
      return {
        id: node.id,
        author: node.author.login,
        authorUrl: node.author.url,
        banner: extractBanner(node.body),
        createdAt: moment(node.createdAt).format('LL'),
        title: node.title,
        labels: node.labels.nodes.map(labelNode => labelNode.name).join(','),
        summary: extractSummary(node.body),
        body: node.body
      };
    } else {
      return null;
    }
  }

  getPost(id): Observable<Post> {
    let query = `query {
      node(id: "${id}") {
        ... on Issue {
          id
          author {
            login
            url
          }
          body
          createdAt
          title
          url
          labels(first: 5) {
            nodes {
              name
            }
          }
        }
      }
    }`;

    return this.graphql.query(query).pipe(
      map(res => this.getPostFrom(res.node)),
      catchError(() => of<Post>(null))
    );
  }

  getPosts({
    after = null,
    before = null,
    category = null
  } = {}): Observable<PostPage> {
    let queryParam = `states: OPEN, orderBy: { direction: DESC, field: CREATED_AT }`;
    let direction = 'first';

    if (after) {
      queryParam += `, after: "${after}"`;
    }

    if (before) {
      queryParam += `, before: "${before}"`;
      direction = 'last';
    }

    if (category) {
      queryParam += `, labels: "${category}"`;
    }

    queryParam += `, ${direction}: ${this.itemPerPage}`;

    let query = `query {
      repository(owner: "xieziyu", name: "xieziyu.github.io") {
        issues(${queryParam}) {
          edges {
            cursor
            node {
              id
              author {
                login
                url
              }
              body
              createdAt
              title
              url
              labels(first: 5) {
                nodes {
                  name
                }
              }
            }
          }
          pageInfo {
            endCursor
            hasNextPage
            hasPreviousPage
            startCursor
          }
        }
      }
    }`;

    return this.graphql.query(query).pipe(
      map(res => res.repository.issues),
      map(issues => {
        let postPage: PostPage = this.getEmptyPage();
        postPage.posts = issues.edges.map(edge => this.getPostFrom(edge.node));
        postPage.pageInfo = { ...issues.pageInfo };

        return postPage;
      }),
      catchError(() => of<PostPage>(this.getEmptyPage()))
    );
  }

  getLabelCount(category: string) {
    let query = `query {
      repository(owner: "xieziyu", name: "xieziyu.github.io") {
        issues(first:0, labels:"${category}") {
          totalCount
        }
      }
    }`;

    return this.graphql.query(query).pipe(
      map(res => res.repository.issues.totalCount)
    );
  }
}
