import { EntityState, EntityAdapter, createEntityAdapter } from '@ngrx/entity';
import { Post } from '../shared/models/post.model';
import { PostActionTypes, PostActions } from '../actions/post.actions';

export interface State extends EntityState<Post> {
  selectedPostId: string | null;
}

export const adapter: EntityAdapter<Post> = createEntityAdapter<Post>();

export const initialState: State = adapter.getInitialState({
  selectedPostId: null,
});

export function reducer(state = initialState, action: PostActions): State {
  switch (action.type) {
    case PostActionTypes.LoadPosts: {
      return adapter.addAll(action.payload.posts, state);
    }

    case PostActionTypes.LoadOnePost: {
      return adapter.addOne(action.payload.post, state);
    }

    case PostActionTypes.ClearPosts: {
      return adapter.removeAll({ ...state, selectedPostId: null });
    }

    case PostActionTypes.SelectPost: {
      return { ...state, selectedPostId: action.payload.id };
    }

    default:
      return state;
  }
}

export const getSelectedPostId = (state: State) => state.selectedPostId;

export const {
  // select the array of post ids
  selectIds: selectPostIds,

  // select the dictionary of post entities
  selectEntities: selectPostEntities,

  // select the array of posts
  selectAll: selectAllPosts,

  // select the total post count
  selectTotal: selectPostTotal,
} = adapter.getSelectors();
