import { Injectable } from '@angular/core';
import { Store, select } from '@ngrx/store';
import { Actions, Effect, ofType } from '@ngrx/effects';
import { map, exhaustMap, tap, withLatestFrom } from 'rxjs/operators';
import { PostsService } from '../shared/services/posts.service';
import { State, selectPageInfo } from '../reducers';

import {
  LoadPage,
  LoadPageSuccess,
  GoNextPage,
  GoPrevPage,
  PostPageActionTypes
} from '../actions/post-page.actions';

import {
  LoadPosts,
  ClearPosts,
  PostActions,
  PostActionTypes
} from '../actions/post.actions';

import { PostPage } from '../shared/models/post-page.model';
import * as fromPostPage from '../reducers/post-page.reducer';

@Injectable()
export class PostPageEffects {

  @Effect()
  loadPage$ = this.actions$.pipe(
    ofType(PostPageActionTypes.LoadPage),
    map((action: LoadPage) => action.payload),
    exhaustMap((payload) =>
      this.posts.getPosts({
        category: payload.category
      }).pipe(
        map((postPage: PostPage) => new LoadPageSuccess(postPage))
      )
    )
  );

  @Effect()
  loadPageSuccess$ = this.actions$.pipe(
    ofType(PostPageActionTypes.LoadPageSuccess),
    map((action: LoadPageSuccess) => action.payload),
    map(postPage => new LoadPosts(postPage))
  );

  @Effect()
  goNextPage$ = this.actions$.pipe(
    ofType(PostPageActionTypes.GoNextPage),
    withLatestFrom<GoNextPage, fromPostPage.State>(this.store.pipe(select(selectPageInfo))),
    exhaustMap(([action, state]) =>
      this.posts.getPosts({
        category: state.category,
        after: state.endCursor
      }).pipe(
        map((postPage: PostPage) => new LoadPageSuccess(postPage))
      )
    )
  );

  @Effect()
  goPrevPage$ = this.actions$.pipe(
    ofType(PostPageActionTypes.GoPrevPage),
    withLatestFrom<GoPrevPage, fromPostPage.State>(this.store.pipe(select(selectPageInfo))),
    exhaustMap(([action, state]) =>
      this.posts.getPosts({
        category: state.category,
        before: state.startCursor
      }).pipe(
        map((postPage: PostPage) => new LoadPageSuccess(postPage))
      )
    )
  );

  constructor(
    private actions$: Actions,
    private posts: PostsService,
    private store: Store<State>
  ) {}
}
