import {
  ActionReducer,
  ActionReducerMap,
  createFeatureSelector,
  createSelector,
  MetaReducer
} from '@ngrx/store';
import * as fromRouter from '@ngrx/router-store';
import { environment } from '../../environments/environment';
import { storeFreeze } from 'ngrx-store-freeze';
import { RouterStateUrl } from '../shared/utils/router-state';

import * as fromPost from './post.reducer';
import * as fromPostPage from './post-page.reducer';

export interface State {
  posts: fromPost.State;
  pageInfo: fromPostPage.State;
  router: fromRouter.RouterReducerState<RouterStateUrl>;
}

export const reducers: ActionReducerMap<State> = {
  posts: fromPost.reducer,
  pageInfo: fromPostPage.reducer,
  router: fromRouter.routerReducer,
};

export const metaReducers: MetaReducer<State>[] = !environment.production ? [storeFreeze] : [];

// Posts Selectors
export const selectPosts = (state: State) => state.posts;

export const selectPostIds = createSelector(
  selectPosts,
  fromPost.selectPostIds
);

export const selectPostEntities = createSelector(
  selectPosts,
  fromPost.selectPostEntities
);

export const selectAllPosts = createSelector(
  selectPosts,
  fromPost.selectAllPosts
);

export const selectPostTotal = createSelector(
  selectPosts,
  fromPost.selectPostTotal
);

export const selectCurrentPostId = createSelector(
  selectPosts,
  fromPost.getSelectedPostId
);

export const selectCurrentPost = createSelector(
  selectPostEntities,
  selectCurrentPostId,
  (postEntities, postId) => postEntities[postId]
);

// Page Info Selectors
export const selectPageInfo = (state: State) => state.pageInfo;

export const hasNextPage = createSelector(
  selectPageInfo,
  (state: fromPostPage.State) => state.hasNextPage
);

export const hasPreviousPage = createSelector(
  selectPageInfo,
  (state: fromPostPage.State) => state.hasPreviousPage
);

export const isPageLoading = createSelector(
  selectPageInfo,
  (state: fromPostPage.State) => state.loading
);

export const selectEndCursor = createSelector(
  selectPageInfo,
  (state: fromPostPage.State) => state.endCursor
);

export const selectStartCursor = createSelector(
  selectPageInfo,
  (state: fromPostPage.State) => state.startCursor
);

// Router Selectors
export const selectRouter = (state: State) => state.router;

export const selectRouterParam = createSelector(
  selectRouter,
  (state: fromRouter.RouterReducerState<RouterStateUrl>) => state.state.params
);
