import { Component, OnInit } from '@angular/core';
import { Store, select } from '@ngrx/store';

import * as fromRoot from '../../reducers';
import { LoadPage, GoNextPage, GoPrevPage } from '../../actions/post-page.actions';

@Component({
  selector: 'blog-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.scss']
})
export class HomeComponent implements OnInit {
  posts$ = this.store.pipe(select(fromRoot.selectAllPosts));
  hasNext$ = this.store.pipe(select(fromRoot.hasNextPage));
  hasPrev$ = this.store.pipe(select(fromRoot.hasPreviousPage));
  loading$ = this.store.pipe(select(fromRoot.isPageLoading));

  constructor(private store: Store<fromRoot.State>) { }

  ngOnInit() {
    window.scrollTo(0, 0);

    this.store.dispatch(new LoadPage({ category: null }));
  }

  nextPage() {
    this.store.dispatch(new GoNextPage());
  }

  prevPage() {
    this.store.dispatch(new GoPrevPage());
  }
}
