import { Component, OnInit, OnDestroy } from '@angular/core';
import { Store, select } from '@ngrx/store';
import { Subscription } from 'rxjs';
import { take, map, filter, tap } from 'rxjs/operators';

import * as fromRoot from '../../reducers';
import { LoadPage, GoNextPage, GoPrevPage } from '../../actions/post-page.actions';

@Component({
  selector: 'blog-filtered-page',
  templateUrl: './filtered-page.component.html',
  styleUrls: ['./filtered-page.component.scss']
})
export class FilteredPageComponent implements OnInit, OnDestroy {
  posts$ = this.store.pipe(select(fromRoot.selectAllPosts));
  hasNext$ = this.store.pipe(select(fromRoot.hasNextPage));
  hasPrev$ = this.store.pipe(select(fromRoot.hasPreviousPage));
  loading$ = this.store.pipe(select(fromRoot.isPageLoading));
  observer: Subscription;

  constructor(private store: Store<fromRoot.State>) { }

  ngOnInit() {
    window.scrollTo(0, 0);

    this.observer = this.store.pipe(
      select(fromRoot.selectRouterParam),
      map(param => param.category),
      filter(category => !!category),
      tap(category => this.store.dispatch(new LoadPage({ category })))
    ).subscribe();
  }

  ngOnDestroy() {
    this.observer.unsubscribe();
  }

  nextPage() {
    this.store.dispatch(new GoNextPage());
  }

  prevPage() {
    this.store.dispatch(new GoPrevPage());
  }

}
